import { forwardRef, memo, useState } from 'react';
import cn from 'classnames';

import { Label } from '../Label';

import { InputProps } from './InputPassword.types';
// TODO: replace with own icons later. Temporarily used font awesome 5 icons
import { EyeIcon } from './icons/EyeIcon';
import { EyeSlashIcon } from './icons/EyeSlashIcon';

const RCN = 'thuiInputPassword';

export const InputPassword = memo(
  forwardRef<HTMLInputElement, InputProps>(
    (
      {
        id,
        name,
        label,
        size = 'md',
        showPassword = false,
        changeVisibility,
        // if user doesn't pass custom icons use default
        eyeIcon = <EyeIcon />,
        eyeSlashIcon = <EyeSlashIcon />,
        isInvalid,
        isRequired = false,
        className,
        disabled,
        value,
        onChange,
        error,
        ...restProps
      }: InputProps,
      ref
    ) => {
      const rootClasses = cn(RCN, className, `${RCN}--${size}`, {
        [`${RCN}--invalid`]: isInvalid,
        [`${RCN}--filled`]: !!value
      });

      const isUncontrolled = !onChange;
      const controls = isUncontrolled ? {} : { value, onChange };

      const [isPasswordVisible, setIsPasswordVisible] = useState(showPassword);
      const isVisibilityUncontrolled = !changeVisibility;
      const visibilityControls = isVisibilityUncontrolled
        ? { onClick: () => setIsPasswordVisible(!isPasswordVisible) }
        : { onClick: changeVisibility };
      const visibilityState = isVisibilityUncontrolled ? isPasswordVisible : showPassword;

      return (
        <div className={rootClasses}>
          <div className={`${RCN}__input-wrapper`}>
            <input
              ref={ref}
              id={id}
              name={name}
              type={visibilityState ? 'text' : 'password'}
              className={`${RCN}__input`}
              disabled={disabled}
              {...controls}
              {...restProps}
            />
            <div className={`${RCN}__eye-icon`} {...visibilityControls}>
              {visibilityState ? eyeIcon : eyeSlashIcon}
            </div>
          </div>
          {label && (
            <Label
              label={label}
              htmlFor={name}
              isRequired={isRequired}
              className={`${RCN}__label`}
              disabled={disabled}
            />
          )}
          {error && <div className={`${RCN}__error`}>{error}</div>}
        </div>
      );
    }
  )
);
